// react:
import React, { PureComponent } from "react";

// react-router:
import { withRouter } from "react-router";

// redux:
import { connect } from "react-redux";

// main components:
import MyAppBar from "shared/components/general/MyAppBar.jsx";

// material ui:
import { withStyles } from "@material-ui/core/styles";

// components:
import { Button, MenuItem, Menu, IconButton } from "@material-ui/core";
import { fade } from "@material-ui/core/styles/colorManipulator";

// icons:
import MoreIcon from "@material-ui/icons/MoreVert";
import MenuIcon from "@material-ui/icons/Menu";
import SearchIcon from "@material-ui/icons/Search";
import AccountCircle from "@material-ui/icons/AccountCircle";

// recompose:
import { compose } from "recompose";

// jss:
const styles = (theme) => ({
	root: {},
	grow: {
		flexGrow: 1,
	},
});

class StreamsAppBar extends PureComponent {
	constructor(props) {
		super(props);
	}

	componentDidMount() {}

	handleLoginRegister = () => {
		this.props.history.push("/login");
	};

	handleAccount = () => {
		this.props.history.push("/account");
	};

	handleDownloadHostFiles = () => {
		window.open("https://remotegames.io/8099/download/", "_blank");
	};

	handleProjectDiscord = () => {
		window.open("https://discord.io/rgio/", "_blank");
	};

	handleDevDiscord = () => {
		window.open("https://discord.io/fosse/", "_blank");
	};

	render() {
		console.log("re-rendering streamsappbar.");

		const { classes } = this.props;

		if (this.props.hide) {
			return null;
		}

		const mobileMenu = (
			<div>
				<MenuItem onClick={this.handleAccount}>
					<IconButton color="inherit">
						<AccountCircle />
					</IconButton>
					<p>Profile</p>
				</MenuItem>
				<MenuItem onClick={this.handleDownloadHostFiles}>
					<p>Download Host Files</p>
				</MenuItem>
				<MenuItem onClick={this.handleProjectDiscord}>
					<p>Project Discord Server</p>
				</MenuItem>
				<MenuItem onClick={this.handleDevDiscord}>
					<p>Dev's Discord Server</p>
				</MenuItem>
			</div>
		);

		let desktop = (
			<>
				<div className={classes.grow} />
				<Button
					size="small"
					color="default"
					variant="contained"
					onClick={this.handleDownloadHostFiles}
				>
					Download Host Files
				</Button>
				<div style={{ width: "10px" }} />
				<Button
					size="small"
					color="default"
					variant="contained"
					onClick={this.handleProjectDiscord}
				>
					Project Discord
				</Button>
				<div style={{ width: "10px" }} />
				<Button
					size="small"
					color="default"
					variant="contained"
					onClick={this.handleDevDiscord}
				>
					Dev's Discord
				</Button>
				<IconButton onClick={this.handleAccount} color="inherit">
					<AccountCircle />
				</IconButton>
			</>
		);

		let mobile = <div></div>;

		if (!this.props.loggedIn) {
			mobile = desktop = (
				<>
					<div style={{ width: "10px" }} />
					<Button
						size="small"
						color="default"
						variant="contained"
						onClick={this.handleLoginRegister}
					>
						Login / Register
					</Button>
				</>
			);
		}

		return (
			<MyAppBar desktop={desktop} mobile={mobile} mobileMenu={mobileMenu}></MyAppBar>
		);
	}
}

const mapStateToProps = (state) => {
	return {
		loggedIn: state.client.loggedIn,
	};
};

const mapDispatchToProps = (dispatch) => {
	return {};
};

export default compose(
	withRouter,
	withStyles(styles),
	connect(mapStateToProps, mapDispatchToProps),
)(StreamsAppBar);
